"use client";

import { useState } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import Image from "next/image";

import { zodResolver } from "@hookform/resolvers/zod";
import { RiGithubFill } from "@remixicon/react";
import { useForm } from "react-hook-form";
import { toast } from "sonner";

import { signUp, signIn } from "@/lib/auth-client";
import { SignUpFormData, signUpSchema } from "@/lib/validations/auth";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";

import { Input } from "../ui/input";
import { Label } from "../ui/label";

export function SignUpForm() {
  const router = useRouter();
  const [loadingButtons, setLoadingButtons] = useState({
    google: false,
    email: false,
    github: false,
  });
  const [profileImage, setProfileImage] = useState<string | null>(null);

  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm<SignUpFormData>({
    resolver: zodResolver(signUpSchema),
  });

  const [generalError, setGeneralError] = useState<string | null>(null);

  const handleSignUpSocial = async (provider: "google" | "github") => {
    setLoadingButtons((prevState) => ({ ...prevState, [provider]: true }));
    try {
      await signIn.social({
        provider: provider,
        callbackURL: "/dashboard",
      });
    } catch (error) {
      setGeneralError(
        error instanceof Error ? error.message : "An unknown error occurred"
      );
      toast.error(
        error instanceof Error ? error.message : "An unknown error occurred"
      );
    } finally {
      setLoadingButtons((prevState) => ({ ...prevState, [provider]: false }));
    }
  };

  const handleSignUpEmail = async (data: SignUpFormData) => {
    try {
      setLoadingButtons((prevState) => ({ ...prevState, email: true }));
      setGeneralError(null);

      await signUp.email({
        name: data.name,
        email: data.email,
        password: data.password,
        image: data.profileImage,
        callbackURL: "/dashboard",
      });
      toast.success("Account created successfully! Redirecting...");
      router.push("/dashboard");
    } catch (error) {
      setGeneralError(
        error instanceof Error ? error.message : "An unknown error occurred"
      );
      toast.error(
        error instanceof Error ? error.message : "An unknown error occurred"
      );
    } finally {
      setLoadingButtons((prevState) => ({ ...prevState, email: false }));
    }
  };
}
